.footer{
    margin: 50px 170px;
    display: flex;
    flex-direction: column;
    gap:30px;

}
.footer-top{
    display: flex;
    justify-content: space-between;

}
.footer-top-left p{
    font-size: 18px;
    max-width: 400px;

}
.footer-top-right{
    display: flex;
    align-items: center;
    gap:35px;
}
.footer-email-input{
    display: flex;
    gap:30px;
    padding: 20px 30px ;
    border-radius: 50px;
    background: #32323B;
    padding-right: 80px;
}
.footer-email-input input{
    outline: none;
    border: none;
    background: transparent;
    color:#a0a0a0;
    font-family: Outfit;
    font-size: 18px;
    transition: 0.5s;
}
.footer-email-input:hover{
 
    transition: 0.5s;
    box-shadow: 0px 0px 30px #037ADE;
}
.footer-subscribe{
    font-size: 20px;
    padding: 22px 50px;
    border-radius: 50px;
    background: linear-gradient(264deg, #037ADE -5.09%,#03E5B7 106.28%);
    cursor: pointer;
    transition: 0.3s;

}
.footer-subscribe:hover{
    transform: scale(1.1);
    background: linear-gradient(500deg, #037ADE -5.09%,#03E5B7 106.28%);
    transition: 0.3s;
    box-shadow: 0px 0px 20px #037ADE
}

.footer-bottom{
    display: flex;
    justify-content: space-between;
    font-size: 18px;
}
.footer-bottom-right{
    display: flex;
    gap:50px;
    margin-bottom: 50px;
    
}
@media(max-width:768px){
    .footer{
        margin: 50px 70px;
    }
    .footer-top{
       flex-direction: column;
       gap:50px;
    }
    .footer-bottom{
        display: flex;
        flex-direction: column-reverse;
        margin-bottom: 50px;
    }
}

@media(max-width:480px){
    .footer{
        margin: 20px;
    }
    .footer-top-right{
        flex-direction: column;
    }
    .footer-email-input input{
        width: 150px;
    }
    .footer-subscribe{
        font-size: 15px;
        padding: 20px 30px;
    }
}