.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:80px;
    margin: 80px 170px;

}
.contact-title{
    position: relative;
}
.contact-title h1{
    padding: 0px 30px;
    font-size: 70px;
    font-weight: 500;
    background: linear-gradient(270deg, #037ADE 10%, #03E5B7 100%);
    background-clip:text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.contact-title img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}
.contact-section{
    display: flex;
    gap: 150px;

}
.contact-left{
    display: flex;
    flex-direction: column;
    gap:30px;

}

.contact-left h1{
    font-size: 70px;
    font-weight: 600;
    background: linear-gradient(270deg, #037ADE -5.09%, #03E5B7 100%);
    background-clip:text ;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.contact-left p{
    max-width: 550px;
    color:#D8D8D8;
    font-size: 18px;
    line-height: 35px;
}
.contact-details{
    display: flex;
    flex-direction: column;
    gap:30px;
    color: #D8D8D8;
    font-size: 20px;
}
.contact-detail{
    display: flex;
    align-items: center;
    gap:20px
}
.contact-right{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap:30px;
}
.contact-right label{
    color:#D8D8D8;
    font-size: 20px;
    font-weight: 500;
}
.contact-right input{
    border:none;
    width:700px;
    height: 78px;
    padding-left:20px ;
    border-radius: 4px;
    background: #32323c ;
    color:#A0A0A0;
    font-family: Outfit;
    font-size: 20px;
}
.contact-right input:hover{
    box-shadow: 0px 0px 30px #037ADE ;
}
.contact-right textarea{
    width:650px;
    border:none;
    padding: 25px;
    border-radius: 4px;
    background: #32323c;
    color:#A0A0A0;
    font-family: Outfit;
    font-size: 20px;
}
.contact-right textarea:hover{
    box-shadow: 0px 0px 30px #037ADE;
}
.contact-submit{
    border: none;
    color:white;
    border-radius: 50px;
    background: linear-gradient(264deg, #037ADE  -5.09%,#03E5B7 100%);
    font-size: 22px;
    padding: 20px 60px;
    margin-bottom:50px;
    cursor: pointer;
    transition: 0.3s;

    
}
.contact-submit:hover{
    transform: scale(1.1);
    transition: 0.3s;
    background: linear-gradient(500deg, #037ADE  -5.09%,#03E5B7 100%);
    box-shadow: 0px 0px 20px #037ADE
}

@media(max-width:768px){
    .contact{
        gap:50px;
        margin: 50px 70px;
        align-items: start;

    }

    .contact-title h1{
        padding: 0;
        font-size: 50px;
        font-weight: 500;
    }
    .contact-section{
        flex-direction: column;

    }
    .contact-left h1{
        font-size: 54px;

    }
    .contact-left p{
        font-size: 18px;
    }
    .contact-detail{
        gap:20px;
    }
    .contact-detail img{
        width: 25px;

    }
    .contact-right label{
        font-size: 20px;

    }
    .contact-right input{
        width: 100%;
        font-size: 20px;

    }
    .contact-right textarea{
        width: 95%;
        font-size: 20px;
    }
    .contact-submit{
        font-size: 20px;
        padding: 15px 40px;
    }
}
@media(max-width:480px){
    .contact{
        margin: 0;
       gap:40px;
    }
    .contact-title h1{
        margin-top: 50px;
        margin-left: 50px;
    }
    .contact-left h1{
        font-size: 40px;
        margin-left: 20px;
    }
    .contact-left p{
        font-size: 20px;
        margin-left:20px ;
    }
    .contact-detail img{
        width:20px;
        margin-left: 20px;
    }
    .contact-right label{
        margin-left: 20px;
    }
    .contact-right input{
        width: 300px;
        margin-left: 20px;
    }
    .contact-right textarea{
        width: 270px;
        margin-left: 20px;
    }
    .contact-submit{
        margin-left: 100px;
}
}